import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { NotificationRouting } from '@backbase/notifications-common-ang';

@Injectable()
export class NotificationsPopupsRouterService {
  private readonly popupsRoutingSubject = new Subject<NotificationRouting>();
  /**
   * Popups notification routing stream
   */
  readonly popupsRouting$: Observable<NotificationRouting> = this.popupsRoutingSubject.asObservable();

  /**
   * Emit popups routing
   *
   * @param routing Notification routing data
   */
  emitPopupsRouting(routing: NotificationRouting) {
    this.popupsRoutingSubject.next(routing);
  }
}
